const app = getApp()
export default {
	data() {
		return {
			disabled: false,
			currentTime: 60, // 倒计时初始值
			time: '获取验证码',
			mobile: '',
			verification_key: ''
		}
	},
	methods: {
		inputVal(e){
			this.mobile = e.detail.value
			console.log(e)
		},
		inputCode(e){
			this.code = e.detail.value
			console.log(e)
		},
		getCode(){
			if (this.disabled) {
				return false;
			}
			if (this.mobile == '') {
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.mobile.length != 11) {
				uni.showToast({
					title: '请输入正确的手机号',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			
			this.disabled = true;
			// this.countdown();
			// return
			uni.request({
				url: `${this.$helper.requestUrl}user/send_sms`,
				header: {
					authorization: app.globalData.token
				},
				method: 'POST',
				data: {
					mobile: this.mobile
				},
				success: res => {
					console.log(res);
					res = this.$helper.null2str(res.data);
					console.log(res,'****************')
					if (res.status_code == 'ok') {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
						this.verification_key = res.data.verification_key;
						this.countdown();
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		//倒计时
		countdown() {
			console.log('---------------')
			var currentTime = this.currentTime;
			this.time = `倒计时${currentTime}秒`;
			var interval = setInterval(() => {
				this.time = '倒计时' + (currentTime - 1) + '秒';
				currentTime--;
				if (currentTime <= 0) {
					clearInterval(interval);
					this.time = '重新获取';
					this.currentTime = 60;
					this.disabled = false;
				}
			}, 1000);
		},
		bind(){
			if(this.code == ''){
				uni.showToast({
					title: '请输入验证码',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: `${this.$helper.requestUrl}user/bindMobile`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					verification_key: this.verification_key,
					mobile: this.mobile,
					code: this.code
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '-------------------');
					if (res.status_code == 'ok') {
						uni.showToast({
							title: res.message,
							icon: 'none',
							duration: 2000
						});
						setTimeout(e =>{
							uni.navigateBack({
								data: 1
							})
						},2000)
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		
		
	}
}
